var phoneInput = new window.ValidationLibrary.PhoneInput();
var phone = document.querySelector('.phone');
var phoneList = document.querySelector('.phone--list');
phone.oninput = function () {
    phone.value = phoneInput.getValidatedPhone(phone.value);
};
phone.onclick = function () {
    phoneInput.addPhonePrefix();
    phone.value = phoneInput.phone;
};
phone.onblur = function () {
    phoneInput.removePhonePrefix();
    phone.value = phoneInput.getValidatedPhone(phone.value);
};
phone.onkeypress = function (event) {
    if (event.which == 13 || event.keyCode == 13) {
        var number = String(phoneInput.getPhoneNumber());
        if (number.length === phoneInput.limit) {
            var li = document.createElement('li');
            li.innerHTML = phoneInput.phone;
            phoneList.appendChild(li);
            phoneInput.phone = '';
            phone.value = '';
        }
    }
};



